@use "sass:map";
@use "sass:meta";
@use "./../utilities/breakpoints" as *;

// padding + margine
$properties: (
  "p": padding,
  "m": margin,
);

$direction: (
  "t": top,
  "r": right,
  "b": bottom,
  "l": left,
  "x": (
    left,
    right,
  ),
  "y": (
    top,
    bottom,
  ),
);

// Base utilities (non-responsive)
@each $short, $property in $properties {
  @for $i from 0 through 30 {
    $val: $i * 5;
    .#{$short}-#{$val}px {
      #{$property}: #{$val}px !important;
    }
  }

  @each $side-short, $side in $direction {
    @for $i from 0 through 30 {
      $val: $i * 5;
      .#{$short}#{$side-short}-#{$val}px {
        @if meta.type-of($side) == "list" {
          @each $s in $side {
            #{$property}-#{$s}: #{$val}px !important;
          }
        } @else {
          #{$property}-#{$side}: #{$val}px !important;
        }
      }
    }
  }
}

// Responsive utilities (Bootstrap style)
@each $breakpoint, $min-width in $breakpoints {
  @media (min-width: #{$min-width}) {
    @each $short, $property in $properties {
      @for $i from 0 through 30 {
        $val: $i * 5;
        .#{$short}-#{$breakpoint}-#{$val}px {
          #{$property}: #{$val}px !important;
        }
      }

      @each $side-short, $side in $direction {
        @for $i from 0 through 30 {
          $val: $i * 5;
          .#{$short}#{$side-short}-#{$breakpoint}-#{$val}px {
            @if meta.type-of($side) == "list" {
              @each $s in $side {
                #{$property}-#{$s}: #{$val}px !important;
              }
            } @else {
              #{$property}-#{$side}: #{$val}px !important;
            }
          }
        }
      }
    }
  }
}
