@use "sass:map";
@use "sass:meta";
@use "./../utilities/breakpoints" as *;

// Size properties: only include what you need
$size-properties: (
  "w": width,
  "max-w": max-width,
  "h": height,
);

// Step-based size values
$sizes: ();
$value: 0;
@while $value <= 1600 {
  $sizes: map.merge(
    $sizes,
    (
      $value: $value,
    )
  );
  @if $value < 200 {
    $value: $value + 5;
  } @else if $value < 400 {
    $value: $value + 10;
  } @else if $value < 1000 {
    $value: $value + 50;
  } @else {
    $value: $value + 100;
  }
}

// Non-responsive utility classes for width/height
@each $short, $property in $size-properties {
  @each $val, $actual in $sizes {
    .#{$short}-#{$val}px {
      #{$property}: #{$actual}px !important;
    }
  }
}

// Responsive width/height utility classes
@each $breakpoint, $min-width in $breakpoints {
  @media (min-width: #{$min-width}) {
    @each $short, $property in $size-properties {
      @each $val, $actual in $sizes {
        .#{$short}-#{$breakpoint}-#{$val}px {
          #{$property}: #{$actual}px !important;
        }
      }
    }
  }
}

// Position utility generation (top, right, bottom, left)
$sides: (top, right, bottom, left);

// Non-responsive position utilities
@each $side in $sides {
  $spacing: 0;
  @while $spacing <= 300 {
    .#{$side}-#{$spacing}px {
      #{$side}: #{$spacing}px !important;
    }
    .#{$side}-minus-#{$spacing}px {
      #{$side}: -#{$spacing}px !important;
    }

    @if $spacing < 30 {
      $spacing: $spacing + 1;
    } @else {
      $spacing: $spacing + 10;
    }
  }

  // Percent-based positioning
  $spacing: 0;
  @while $spacing <= 100 {
    .#{$side}-#{$spacing}p {
      #{$side}: #{$spacing + "%"} !important;
    }
    .#{$side}-minus-#{$spacing}p {
      #{$side}: -#{$spacing + "%"} !important;
    }
    $spacing: $spacing + 5;
  }
}

// Responsive position utility classes
@each $breakpoint, $min-width in $breakpoints {
  @media (min-width: #{$min-width}) {
    @each $side in $sides {
      $spacing: 0;
      @while $spacing <= 300 {
        .#{$side}-#{$breakpoint}-#{$spacing}px {
          #{$side}: #{$spacing}px !important;
        }
        .#{$side}-#{$breakpoint}-minus-#{$spacing}px {
          #{$side}: -#{$spacing}px !important;
        }

        @if $spacing < 30 {
          $spacing: $spacing + 1;
        } @else {
          $spacing: $spacing + 10;
        }
      }

      // Percent-based responsive
      $spacing: 0;
      @while $spacing <= 100 {
        .#{$side}-#{$breakpoint}-#{$spacing}p {
          #{$side}: #{$spacing + "%"} !important;
        }
        .#{$side}-#{$breakpoint}-minus-#{$spacing}p {
          #{$side}: -#{$spacing + "%"} !important;
        }
        $spacing: $spacing + 5;
      }
    }
  }
}
