@use "sass:map";
@use "sass:meta";
@use "./../utilities" as *;

// Box shadows
$shadow-sizes: sm, md, lg, xl, xxl, 3xl, modern, none;
@each $size in $shadow-sizes {
  .box-shadow-#{$size},
  .box-shadow-hover-#{$size}:hover {
    box-shadow: var(--wc-shadow-#{$size});
  }
  .box-shadow-#{$size},
  .box-shadow-hover-#{$size} {
    @include transition(0.3s);
  }
}

// Transforms
$transform-values: top 5 -5px, top 10 -10px, top 20 -20px, top 50 -50%, top 100 -100%, bottom 5 5px, bottom 10 10px, bottom 20 20px, bottom 50 50%, bottom 100 100%, left 5 -5px, left 10 -10px, left 20 -20px, left 50 -50%, left 100 -100%, right 5 5px, right 10 10px, right 20 20px, right 50 50%, right 100 100%, scale 1 1.025, scale 2 1.05, scale 3 1.075, scale 4 1.1, scale 5 1.2;
@each $prop, $size, $value in $transform-values {
  .transform-#{$prop}-#{$size},
  .transform-hover-#{$prop}-#{$size}:hover {
    transform: #{if($prop == "scale", "scale", "translate" + if($prop == "top" or $prop == "bottom", "Y", "X")) }(#{$value});
  }
  .transform-#{$prop}-#{$size},
  .transform-hover-#{$prop}-#{$size} {
    @include transition(0.3s);
  }
}
