@use "sass:map";
@use "sass:meta";
@use "./../utilities/mixins" as *;

$theme-colors: (
  "primary": var(--wc-primary),
  "primary-alt": var(--wc-primary-alt),
  "brand": var(--wc-brand),
  "brand-alt": var(--wc-brand-alt),
  "light": var(--wc-light),
  "light-alt": var(--wc-light-alt),
  "border": var(--wc-border),
  "menu-color": var(--wc-menu-color),
);

$colors: (
  "white": var(--wc-white),
  "muted": var(--wc-muted),
  "primary": var(--wc-primary),
  "secondary": var(--wc-secondary),
  "success": var(--wc-success),
  "info": var(--wc-info),
  "warning": var(--wc-warning),
  "danger": var(--wc-danger),
  "dark": var(--wc-dark),
  "light": var(--wc-light),
  "black": var(--wc-black),
);

$gray-shades: (
  100: var(--bs-gray-100),
  200: var(--bs-gray-200),
  300: var(--bs-gray-300),
  400: var(--bs-gray-400),
  500: var(--bs-gray-500),
  600: var(--bs-gray-600),
  700: var(--bs-gray-700),
  800: var(--bs-gray-800),
  900: var(--bs-gray-900),
);

// Text colors
@each $name, $value in $colors {
  .tx-color-#{$name},
  .tx-color-hover-#{$name}:hover {
    color: #{$value} !important;
  }
  .tx-color-#{$name},
  .tx-color-hover-#{$name} {
    @include transition(0.3s);
  }
}

@each $shade, $value in $gray-shades {
  .tx-gray-#{$shade},
  .tx-gray-hover-#{$shade}:hover {
    color: #{$value} !important;
  }
  .tx-gray-#{$shade},
  .tx-gray-hover-#{$shade} {
    @include transition(0.3s);
  }
}

// Background colors
@each $name, $value in $colors {
  .bg-color-#{$name},
  .bg-color-hover-#{$name}:hover {
    background-color: #{$value} !important;
  }
  .bg-color-#{$name},
  .bg-color-hover-#{$name} {
    @include transition(0.3s);
  }
}

@each $shade, $value in $gray-shades {
  .bg-gray-#{$shade},
  .bg-gray-hover-#{$shade}:hover {
    background-color: #{$value} !important;
  }
  .bg-gray-#{$shade},
  .bg-gray-hover-#{$shade} {
    @include transition(0.3s);
  }
}

// Theme colors
@each $name, $value in $theme-colors {
  // Text Color
  .tx-theme-#{$name},
  .tx-theme-hover-#{$name}:hover {
    color: #{$value} !important;
  }
  .tx-theme-#{$name},
  .tx-theme-hover-#{$name} {
    @include transition(0.3s);
  }

  // Background Color
  .bg-theme-#{$name},
  .bg-theme-hover-#{$name}:hover {
    background-color: #{$value} !important;
  }
  .bg-theme-#{$name},
  .bg-theme-hover-#{$name} {
    @include transition(0.3s);
  }

  // Border Color
  .bd-theme-#{$name},
  .bd-theme-hover-#{$name}:hover {
    border-color: #{$value} !important;
  }
  .bd-theme-#{$name},
  .bd-theme-hover-#{$name} {
    @include transition(0.3s);
  }
}
