@use "sass:map";
@use "sass:meta";
@use "./../utilities/breakpoints" as *;

// Base utilities (non-responsive)
$font-size: 1;
@while $font-size <= 120 {
  .fs-#{$font-size}px {
    font-size: #{$font-size}px !important;
  }
  $font-size: $font-size + 1;
}

// Responsive font-size utilities (Bootstrap-style)
@each $breakpoint, $min-width in $breakpoints {
  @media (min-width: #{$min-width}) {
    $font-size: 1;
    @while $font-size <= 120 {
      .fs-#{$breakpoint}-#{$font-size}px {
        font-size: #{$font-size}px !important;
      }
      $font-size: $font-size + 1;
    }
  }
}

// Font weights
$font-weight: (
  "100": 100,
  "200": 200,
  "300": 300,
  "400": 400,
  "500": 500,
  "600": 600,
  "700": 700,
  "800": 800,
  "900": 900,
);

@each $size, $value in $font-weight {
  .fw-#{$size} {
    font-weight: $value;
  }
}

// Letter spacing
$lt-spacing: (
  "none": 0,
  "sm": 0.5px,
  "md": 1px,
  "lg": 2px,
  "xl": 3px,
  "xxl": 4px,
  "3xl": 5px,
);

@each $size, $value in $lt-spacing {
  .lt-spacing-#{$size} {
    letter-spacing: $value;
  }
}
